import React from 'react';

class Playlist extends React.Component {
  audio;
  time;
  timeElement;
  constructor() {
    super();
    this.state = { playBtn: false,songs : [
      {path:"https://www.dropbox.com/s/a7ysv8vk3w7lfez/brown5-1-site.mp3?dl=1",name:"brown",play:false},
      {path:"https://www.dropbox.com/s/htwuiai5me13e0p/chilled%20child%20-site.mp3?dl=1",name:"glide",play:false},
      {path:"https://www.dropbox.com/s/1quinctrf5kba8e/glide901%20-site.mp3?dl=1",name:"ken",play:false},
      {path:"https://www.dropbox.com/s/tsx8jghxltojtfp/ken18%20-site.mp3?dl=1",name:"ken",play:false},
      {path:"https://www.dropbox.com/s/tsx8jghxltojtfp/ken18%20-site.mp3?dl=1",name:"sikui",play:false},
      {path:"https://www.dropbox.com/s/ebwdyt8hgb55uui/sikui%20kalush%203%20-site.mp3?dl=1",name:"sikui",play:false},
    ] };
  }

  render() {
    return <div style={{minHeight:'600px',height:"auto"}} className="container section">
    <div className="row">
    <div className="col-xl-6 col-md-4 mb-4">    
    <div className="card card-img border-0 ">
        <div className="playlist text-left box-music">
<div style={{width:"85%",margin:"auto"}}>
{
  this.state.songs.map((song,index)=>{
    return (
      <div key={index} onClick={(event)=>{this.play(song,event)}} data-path={song.path} data-name={song.name} className="liner audio-track">{index+1}. <span className="rotating">{song.name} <i className="fas fa-compact-disc fa-spin listening-track"></i></span><span className="float-right time">___</span></div>
    )
  })
}
</div>  
        </div>
    </div>
        </div>  
            <div className="col-xl-6 col-md-8 mb-4 d-flex">    
    <div className="card card-img border-0 ">
    <div className="container text-center ">
          <h1 className="mb-1 title display-3">Music Box</h1>
          <p className="mb-5 about-me">
          music maker sounds baker.<br></br>
    design composing recording mixing
          </p>

        </div>
    </div>
        </div>
    </div>
  <div id="holder">
    {
      this.state.playBtn ?   <i onClick={()=>this.stop()} className="fas fa-stop-circle play" ></i>
:  <i className="fas fa-play-circle play" onClick={()=>{this.next()}}></i>

    }
      <i onClick={()=>{this.next()}} className="fas fa-forward next"></i>
  </div>
  <hr></hr>
        </div>
  }
  next(){
      var random = this.state.songs[Math.floor(Math.random() * this.state.songs.length - 1) + 0];
    if(random !== undefined){
      var element = document.querySelector(`[data-name=${random.name}]`);
      if(element !== null){
        this.play(random,element)
      }
    }
 
    // this.setState({
    //   songs
    // })
  }
  stop(){
    this.audio.pause();
    setTimeout(() => {
      this.setState({
        playBtn:false
       });
    }, 0);
  }
  play(song,event){
  setTimeout(() => {
    this.setState({
      playBtn:true
     });
  }, 0);
    var target = event.target;
    if(target === undefined){
      target = event.querySelector('.audio-track') || event.closest('.audio-track');
    }
    if(target !== undefined || target !== null || typeof target !== "undefined"){
      const reqTarget = target.closest('.audio-track');
      if(reqTarget !== null){
        var selectted =  document.querySelectorAll('.selected');
        selectted.forEach((el)=>{
          el.classList.remove('selected')
        })
        reqTarget.classList.add('selected');
      }
      if(this.audio !== undefined){
        this.audio.pause();
        this.setState({
          playBtn:false
         })
      }
      this.audio= new Audio(song.path);
  
      this.audio.addEventListener('loadedmetadata',()=>{
         this.timeElement = reqTarget.querySelector('.time');
        this.time = parseInt((this.audio.duration / 60) % 60)+":" + parseInt(this.audio.duration % 60);
        this.audio.addEventListener('timeupdate', (event) => {
        
          this.timeElement.innerHTML  =  this.convertElapsedTime(this.audio.currentTime) +"/"+this.time ;
        });
        this.audio.addEventListener('pause', (event) => {
          this.setState({
            playBtn:false
           })
          this.timeElement.innerHTML  =  "___"
        });
      });
    
       this.audio.play();
       this.setState({
        playBtn:true
       })
    }
  
  }
  isMobileDevice() {
    return (typeof window.orientation !== "undefined") || (navigator.userAgent.indexOf('IEMobile') !== -1);
};
  componentDidMount() {
    
    var btnsHolder = document.querySelector('#holder');
    if(btnsHolder !== null){
      if(this.isMobileDevice()){
        var humbur = document.querySelector('.navbar-toggler');
        if(humbur !== null){
          humbur.appendChild(btnsHolder)
        }
      }else{
        var navbar = document.querySelector('.btns--eden');
        if(navbar !== null){
          navbar.appendChild(btnsHolder);
        }        
      }
    }
    this.setState({ someKey: 'otherValue' });
  }
  convertElapsedTime(input){
    
var sec = Math.floor(input %  60);
if(sec < 10){
  sec = 0 + sec;
}
var min = Math.floor(input / 60);
return min + ":" + sec;
  }
}

export default Playlist;
