import React from 'react';
import pickSvg from '../assets/Group 5.svg';
class Picks extends React.Component {
  constructor() {
    super();
    this.state = { contact: false };
  }

  render() {
    return <div className="container section">
<div className="row">
<div className="col-xl-6 col-md-4 mb-4">    
<div className="card card-img border-0 ">
    <img className="mx-auto picks" width="100%"  height="auto" src={pickSvg} />
</div>
    </div>  
        <div className="col-xl-6 col-md-8 mb-4 d-flex ">    
<div className="card card-img border-0 ">
<div className="container text-center my-auto top-text">
      <h1 className="mb-1 title display-3">Sound of Eden</h1>
<div>
  {
    this.state.contact ? <h3 className="mb-5 neon fade-in">
    <em>02532354182<br></br>
edensound@gmail.com</em>
  </h3> :<h3 className="mb-5 ">
        <em>music maker sounds baker.<br></br>
design composing recording mixing</em>
      </h3>
  }
 <a className={this.state.contact ? "btn eden-btn btn-xl js-scroll-trigger open-btn": "btn eden-btn btn-xl js-scroll-trigger"} onClick={()=>{
        this.setState({
          contact:!this.state.contact
        })
      }}  href="#">Contact me</a>
</div>
     
    </div>
</div>
    </div>
</div>
    </div>;
  }

  componentDidMount() {
    this.setState({ someKey: 'otherValue' });
  }
}

export default Picks;
