import React from 'react';
import eden from '../assets/about.jpg';

class Studio extends React.Component {
  constructor() {
    super();
    this.state = { someKey: 'someValue' };
  }

  render() {
    return <div className="container section col-offset-12">
    <div className="row">
    <div className="col-xl-6 col-md-4 mb-4">    
   
    <div className="card card-img border-0 ">
    <div className="container text-center my-auto top-text">
          <h1 className="mb-1 title display-3">About me</h1>
          <p className="mb-5 about-me">
            Eden Vahab is a musician and a sound engineer with a natural ear for music and a big passion
for all sound-music creation. His years spent playing music, writing, recording, collaborating and
performing with musicians (all kind of genre) gave him the experience required to be an
effective producer in the studio.
Eden is playing bass, guitar, drums and vocals, his music touches different styles of music like
Rock, Pop, Electronic, Blues, Reggae, Metal, Trance and more.
He works in his studio and create his music with Ableton, Cubase, Nuendo and Pro Tools
DAW's.
Eden offers specialty services not only in recording and mixing, but also in pre-production and
has the ability to guide your project to an outstanding finish.
At 2013 Eden learned Sound Engineering at Sapir College and graduated with honors.
          </p>
        </div>
    </div>
        </div>  
            <div className="col-xl-6 col-md-8 mb-4 d-flex">    
            <div className="card card-img border-0 ">
        <img style={{top:"95px",position:"relative"}} className="mx-auto rounded profile-img" width="300" height="auto" src={eden} />
    </div>
        </div>
    </div>
        </div>
        
  }

  componentDidMount() {
    this.setState({ someKey: 'otherValue' });
  }
}

export default Studio;
